<template>

    <div >
        <shead headid="$route.query.headid"></shead>
        <div class="wrap  com-panel">

            <div class="topic-nav-box">
                {{lanmu.name}}/{{subtype.name}}
                <div class="el-button-group">

                </div>

                <span @click="shoucangClick" class="el-button el-button--danger is-circle"><i class="el-icon-plus"></i></span>

                <span @click="agreeClick"  class="el-button el-button--success is-circle"><i class="el-icon-thumb"></i></span>
                <span v-if="xinxi.fileurl!=null" @click="downloadHandler"  class="el-button el-button--warning"><i class="el-icon-download"></i>{{xinxi.fileName}}下载</span>
            </div>
            <div class="article-title">
                <div class="avatar">

                    <router-link :to="'/e/myspace?id='+author.id">
                        <img v-if="xinxi.pbtype==1" :src="hostHead+author.touxiang" class="image"/>
                    </router-link>
                    <img v-if="xinxi.pbtype==2" :src="hostHead+author.xiangpian" class="image"/>

                </div>
                <div class="tgroup">
                    <div class="title">{{xinxi.title}}</div>
                    <div class="subtitle">
                        <div class="item" v-if="xinxi.pbtype==2">
                            发布人:{{author.realname}}
                        </div>
                        <div class="item" v-else>
                            发布人:{{author.name}}
                        </div>
                        <div class="item">
                            点击次数:{{xinxi.clickcount}}
                        </div>
                        <div class="item">
                            <i class="el-icon-timer"></i>:{{xinxi.pubtime}}
                        </div>
                    </div>
                </div>
            </div>

            <div class="article-bd">
                <div v-html="xinxi.dcontent"></div>
            </div>


        </div>
        <div v-if="listRecomment.length>0" class="wrap round-block">

            <div  class="video-list">
                <div class="hd">
                    猜你喜欢
                </div>
                <div class="bd">

                    <div v-for="item in listRecomment" :key="item.id" class="item">
                        <div class="card">

                                <div class="pic">
                                    <span class="pointer" @click="navigatePage( `/e/xinxiinfo?headid=${$route.query.headid}&id=${item.id}`)">
                                         <img class="image" :src="hostHead+item.images[0]"/>
                                    </span>
                                </div>
                                <div class="name">
                                    {{item.title}}
                                </div>
                                <div class="price">{{item.lmname}}</div>

                        </div>
                    </div>

                </div>
            </div>


        </div>

        <div class="wrap com-panel">
            <comment xtype="xinxi" :belongid="$route.query.id" :delete-self="true"></comment>
        </div>

         <bottom></bottom>

        <loginmodal :show.sync="openLogin"></loginmodal>

    </div>
</template>

<script>
    import Shead from "./shead";
    import Loginmodal from "./loginmodal";
    import Comment from "./comment"
    import Bottom from "./bottom";
    export default {
        name: "xinxiinfo",
        data(){

           return  {
               hostHead: this.$app.contextPath,
                xinxi: {},
                lanmu: {},
                listXinxi:[],
                openLogin:false,
                listRecomment:[],
                author:{},
                subtype: {}
            }

        },

        async created() {

            await this.getInfo();
            await this.getAuthor(this.xinxi);
            await this.getLanmu(this.xinxi.lmid);
            await this.getSubtype(this.xinxi.subtypeid);
            await this.getRecomment();
        },
        methods: {

            navigatePage(url){
                if(url==null||url=="")
                    return ;
                this.$router.push(url);
                this.$app.key=url;

            },
            async getInfo() {
                let data={id:this.$route.query.id};
                if(this.huiyuan!=null)
                    data.hyid=this.huiyuan.id;
                let {data:res} = await this.$http.post("admin/xinxi/info", data);
                if (res != null && res.data != null) {
                    this.xinxi = res.data;
                }
            },


            isLogin() {
               return this.$app.mycas.isLogin(this.$app.mycas.huiyuan_key);
            },
            async getLanmu(id) {
                let res = await this.$http.post("admin/lanmu/info", {id: id});
                if (res.data != null && res.data.data != null) {
                    this.lanmu = res.data.data;
                }
            },
            async downloadHandler() {

                if (!this.isLogin()) {
                    this.openLogin = true;
                    return;
                }

                window.location=this.hostHead+'/admin/download?url='+this.xinxi.fileurl;


            },
            async getAuthor(xinxi) {
                if (xinxi==null)
                    return ;
                let url="admin/users/load";
                if (xinxi.pbtype==1)
                    url = "admin/huiyuan/info";
                let {data:res} = await this.$http.post(url, {id: xinxi.pubren});
                if (res!=null&&res.state<0){
                    this.$message.error(res.des);
                    return ;
                }
                if (res!= null && res.data!= null) {
                    this.author = res.data;
                }
            },
            async getSubtype(id) {
                let {data:res} = await this.$http.post("admin/subtype/load", {
                    id: id
                });
                if (res!= null && res.data != null) {
                    this.subtype = res.data;
                }
            },
            async shoucangClick() {

                if (!this.isLogin()) {
                    this.openLogin=true;
                    return;
                }
                let params = {
                    "targetid": this.xinxi.id,
                    "targetname": this.xinxi.title,
                    "tupian": this.xinxi.tupian,
                    "hyid": this.huiyuan==null?0:this.huiyuan.id,
                    "xtype": "xinxi",
                    "href": "/e/xinxiinfo.jsp?id=" + this.xinxi.id
                };
                let {data:res} = await this.$http.post("admin/shoucang/save", params);
                if (res!= null) {
                    if (res.stateCode <0) {
                        this.$message.error(res.des);
                        return;
                    }
                    this.$message.success("成功收藏...");

                }


            },//end shoucang
            async agreeClick() {

                if (!this.isLogin()) {
                    this.openLogin=true;
                    return;
                }
                let params = {
                    "targetid": this.xinxi.id,
                    "commentator": this.huiyuan==null?"":this.huiyuan.accountname,
                };
                let res = await this.$http.post("admin/xinxi/agree", params);
                if (res.data != null) {
                    if (res.data.stateCode == -2) {
                        this.$message.error("你已经赞过不能再赞...");
                        return;
                    }
                    this.$message.success("完成...");
                    this.getInfo();
                }


            },
            getRecomment(){
                let url="admin/xinxi/recomment";
                if(this.huiyuan==null)
                    return;

                let data={id:this.xinxi.id};
                if(this.huiyuan!=null)
                    data.hyid=this.huiyuan.id;
                this.$http.post(url,data).then(res=>{
                    console.log(res.data);
                    if(res.data!=null&&res.data.data!=null)
                        this.listRecomment=res.data.data;
                });


            }

        },
        computed:{
           huiyuan(){
              return this.$app.mycas.getAccount(this.$app.mycas.huiyuan_key);
           }

        },
        components: {Bottom, Loginmodal, Shead,Comment}
    }
</script>

<style scoped>

</style>
